import * as React from 'react';

import CanvasControlType from './CanvasControlType';
import CanvasToolboxItem, { IToolboxItem } from './CanvasToolboxItem';

import './CanvasToolboxItemsGroup.scss';

export interface ICanvasToolboxItemsGroupProps {
  groupName: string;
  /* tslint:disable:no-any */
  onToolboxItemClick: (controlType: CanvasControlType, itemProps: any) => void;
  /* tslint:enable:no-any */
  toolboxItems: IToolboxItem[];
}

export default class CanvasToolboxItemsGroup extends React.Component<ICanvasToolboxItemsGroupProps, void> {
  constructor(props: ICanvasToolboxItemsGroupProps) {
      super(props);
  }

  public render(): React.ReactElement<ICanvasToolboxItemsGroupProps> {
    const toolboxItems: React.ReactElement<{}>[] = [];
    for (const item of this.props.toolboxItems) {
      toolboxItems.push(
        <CanvasToolboxItem
          key={Math.random()} // @todo change this later, see #165161
          onToolboxItemClick={this.props.onToolboxItemClick}
          toolboxItem={item}
        />
      );
    }

    return (
      <section className='CanvasToolboxItemsGroup'>
        <header className='CanvasToolboxItemsGroup-header'>{ this.props.groupName }</header>
        <div className='CanvasToolboxItemsGroup-items'>
          { toolboxItems }
        </div>
      </section>
    );
  }
}
